import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Config from './Config' 
import Utils from './Utils'
import SAudio from './Audio'

class Dialog 
{
    constructor(loadingManager, scene, camera, renderer, sound, callback, imagePaths){
        this.imagePaths = imagePaths;
        
        this.imageAssets = [];
        this.buttons = [];

        this.callback = callback;
        this.camera = camera;
        this.scene = scene;
        this.renderer = renderer;
        this.sound = sound;
        this.assetManager = new AssetManager();
        this.raycaster = new THREE.Raycaster(); 
        this.mouse = new THREE.Vector2();
        this.load(loadingManager);

        this.audio = new SAudio(sound);
    }

    disposeSuper(){
        // Dispose only what is created here
        this.assetManager.dispose();
        this.audio.dispose();

        for(let i=0; i<this.imageAssets.length; ++i){
            this.scene.remove(this.imageAssets[i]);
        }
    }

    load = async(loadingManager) => {
        console.log("MenuBar loading...");
        
        // Load all assets. Textures, sounds, models required
        await this.assetManager.loadTextures(loadingManager, this.imagePaths);

        // Create scene
        this.createScene();
        
        loadingManager.onSceneLoaded();
    }

    updateForResizeSuper = () =>{
        for (let i = 0; i < this.buttons.length; ++i) {
            this.buttons[i].updateForResize();
        }      
    }

    getTexture2 = (image, name, transparent, xref, x, yref, y, z, vec = null) => {
        var texture = Utils.getTexture(image, name, transparent, x, y, z, vec);
        this.imageAssets.push(texture);
        return new Texture(texture, xref, x, yref, y);
    }

    createScene = () =>{
    }        
}

export default Dialog 