import * as THREE from 'three'
import Dialog from './Dialog'
import ConfettiExplosion from './ConfettiExplosion';

class LevelCompletionDialog extends Dialog
{
    constructor(loadingManager, scene, camera, renderer, sound, callback){
        var imagePaths = [
            './textures/common/interactions/Playagain.png',
            './textures/common/interactions/Play_the_next_level.png',
            ];
        super (loadingManager, scene, camera, renderer, sound, callback, imagePaths)
        this.levelNum = callback.levelNum;
        if(localStorage.getItem("presentStep") || localStorage.getItem("skipClicked"))
        {
            localStorage.removeItem("presentStep");
            localStorage.removeItem("skipClicked");
        }
    }

    dispose(){
        if(this.confetti) this.scene.remove(this.confetti.object);

        document.removeEventListener( 'mousedown', this.onDocumentMouseDown, false );
        document.removeEventListener( 'touchdown', this.onDocumentMouseDown, false );
        document.removeEventListener( 'mouseup', this.onDocumentMouseUp, false );
        document.removeEventListener( 'touchup', this.onDocumentMouseUp, false );

        this.disposeSuper();
    }

    updateForResize = () =>{
        this.updateForResizeSuper();
    }

    createScene = () =>{
        //adding a transparent background with black color
        const geometry = new THREE.PlaneGeometry( window.innerWidth, window.innerHeight );
        const material = new THREE.MeshBasicMaterial({ 
            opacity: 0.50, 
            color:0xff000000,
            transparent: true,
            side: THREE.DoubleSide, 
            depthWrite: false
        });
        this.transparentPlane = new THREE.Mesh( geometry, material );
        this.transparentPlane.scale.set( window.innerWidth, window.innerHeight, 1 );
        this.scene.add( this.transparentPlane );
        this.x = -100

        if(this.levelNum == 4){
            this.x = 0;
        } else{
            this.x = -100;
        }
        this.play_again = this.getTexture2(this.assetManager.textures[0], "play_again", true, 0, this.x, 0, 0, 0.2);
        this.scene.add(this.play_again.texture)
        
        if(this.levelNum != 4){
            this.play_game = this.getTexture2(this.assetManager.textures[1], "play_next_level", true, 0, 100, 0, 0, 0.2);
            this.scene.add(this.play_game.texture)
        }
     
        this.confetti = new ConfettiExplosion({
            rate: 2, // percent of explosion in every tick - smaller is fewer - be careful, larger than 10 may crash your browser!
            amount: 50, // max amount particle of an explosion
            radius: 300, // max radius of an explosion
            areaWidth: 500, // width of the area 
            areaHeight: 500, // height of the area 
            fallingHeight: 400, // start exploding from Y position
            fallingSpeed: 1, // max falling speed
            colors: [0xff0000, 0xffff00, 0xff00ff, 0xffd700, 0x006400, 0xff4500] // random colors
        });
        this.scene.add( this.confetti.object );

        this.updateForResize();        

        document.addEventListener( 'mousedown', this.onDocumentMouseDown, false );
        document.addEventListener( 'touchdown', this.onDocumentMouseDown, false );
        document.addEventListener( 'mouseup', this.onDocumentMouseUp, false );
        document.addEventListener( 'touchup', this.onDocumentMouseUp, false );
    }     
   
    update = () => {
        if(this.confetti) this.confetti.update();
    }

    onDocumentMouseDown = ( event ) => {
        this.mouse.x = ( event.clientX / this.renderer.domElement.clientWidth ) * 2 - 1;
        this.mouse.y = - ( event.clientY / this.renderer.domElement.clientHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.mouse, this.camera );

        var intersects = this.raycaster.intersectObjects( this.scene.children );

        if ( intersects.length > 0 )
        {
            switch(intersects[0].object.name){
                case "play_again":
                    break;
                case "play_next_level":
                    break;
            }
        }
    }
    
    onDocumentMouseUp = ( event ) => {
        this.mouse.x = ( event.clientX / this.renderer.domElement.clientWidth ) * 2 - 1;
        this.mouse.y = - ( event.clientY / this.renderer.domElement.clientHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.mouse, this.camera );

        var intersects = this.raycaster.intersectObjects( this.scene.children );

        if ( intersects.length > 0 )
        {
            switch(intersects[0].object.name){
                case "play_again":
                    this.scene.remove(this.transparentPlane);
                    this.callback.playAgain();
                    break;
                case "play_next_level":
                    this.scene.remove(this.transparentPlane);
                    this.callback.playNextLevel();
                    break;
            }
        }
    }
}

export default LevelCompletionDialog